import React, { Component } from 'react';

export default class Apteczne extends Component {
  render() {
    return (
      <>
        <h1 className="component-header">Produkty Apteczne</h1>
        <h2 className="component-description">
          W naszej ofercie mamy także bogaty dostęp do produktów z Polskich aptek.
          <br /> Nie wszystkie produkty są dostępne od ręki, niektóre są dostępne na zamówienie, przez co czas
          oczekiwania może wydłużyć się do kilku dni roboczych, o czym klient jest informowany jeszcze przed opłaceniem
          zamówienia.
          <br /> Jeżeli na liście nie ma produktu który Cię interesuje, wyślij zapytanie na maila lub poprzez formularz
          kontaktowy, bardzo prawdopodobne że mimo wszystko będziemy w stanie pomóc.
        </h2>
      </>
    );
  }
}
